import IResourceKey from './IResourceKey';

/**
 * Represents a ResourceKey
 */
class ResourceKey implements IResourceKey {
  private static _lastId: number = 0;

  public id: string;
  public name: string;

  /**
   * Creates an instance of the ResourceKey
   * @param name represents the name of the resource
   */
  public static create(name: string): IResourceKey {
    ResourceKey._lastId++;

    const id: string = ResourceKey._lastId.toString();
    return new ResourceKey(id, name);
  }

  /**
   * Initializes an instance of the ResourceKey
   * @param id represents the resource id
   * @param name represents the resource name
   */
  constructor(id: string, name: string) {
    this.id = id;
    this.name = name;
  }
}

const create: (name: string) => IResourceKey = ResourceKey.create;
/* tslint:disable:export-name */
export default create;
/* tslint:enable:export-name */
